@font-face {
  font-family: Avenir;
  src: url("Avenir-Book.657c3568.ttf");
  font-weight: normal;
}

@font-face {
  font-family: Avenir;
  src: url("Avenir-Heavy.01d9ab74.ttf");
  font-weight: bold;
}

html {
  --main-text-color: #3d4e61;
  font-family: Avenir, sans-serif;
  font-size: 16px;
  font-weight: 300;
}

.search-bar {
  border: 1px solid #e1e7ed;
  border-radius: 4px;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  display: flex;
  box-shadow: 0 1px 8px #3d4e611a;
}

.search-bar:hover {
  box-shadow: 0 4px 4px #00000040;
}

.bar--big {
  height: 68px;
}

.bar--small {
  height: 40px;
}

.search-bar__icon-big {
  background-image: url("Search.ccd3c07b.svg");
  background-repeat: no-repeat;
  min-width: 19px;
  min-height: 19px;
  margin-left: 26px;
}

.search-bar__icon-small {
  background-image: url("search-small.5656828a.svg");
  background-repeat: no-repeat;
  min-width: 11px;
  min-height: 11px;
  margin-left: 13px;
}

.search-bar__input {
  color: var(--main-text-color);
  background: url("Search.ccd3c07b.svg") 0 no-repeat;
  border: none;
  outline: none;
  width: 100%;
  height: 100%;
  padding: 0 32px;
  font-family: Avenir, sans-serif;
  font-weight: 300;
}

.search-bar__input::placeholder {
